p.speech {
    position: relative;
    width: 350px;
    height: 100px;
    overflow-wrap: normal;
    text-align: center;
    line-height: 100px;
    background-color: #fff;
    border: 3px solid #666;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 30px;
    -webkit-box-shadow: 2px 2px 4px #888;
    -moz-box-shadow: 2px 2px 4px #888;
    box-shadow: 2px 2px 4px #888;
  }

  p.speech:before {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    left: 30px;
    top: 100px;
    border: 25px solid;
    border-color: #666 transparent transparent #666;
  }
  

  p.speech:after {
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    left: 38px;
    top: 100px;
    border: 19px solid;
    border-color: #fff transparent transparent #fff;
  }

  .current-bpm {
      font-size: 72px;
      color: rgb(103, 105, 255);
      /* padding-left: 169px; */
  }

  .metronome {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
}

  .on-off {
      width: 85px;
  }

  button {
    border: none;
    background: none;
    display: block;
    width: 10%;
    align-self: center;
  }

  input[type=range] {
      width: 30%;
  }

  .tempo-change {
      width: 25px;
  }

  .slider {
      display: flex;
      justify-content: center;
  }

  @media only screen and (max-width: 600px) {
    .metronome {
        display: flex;
        align-content: center;
    }
    input[type=range] {
        width: 80%;
    }
    .on-off {
        width: 50px;
    }
  }